__webpack_require__.r(__webpack_exports__);
/* harmony default export */ __webpack_exports__["default"] = ("<app-header></app-header>\n<div class=\"container\">\n  <div class=\"header\">INFORMATION ABOUT RESEARCH</div>\n  <p class=\"research-para m-t-20\">The activities we will send to your students are based on decades of research at top\n    tier universities, including Stanford and Yale. Thousands of hours of careful and rigorous research went into their\n    design.</p>\n  <p class=\"research-para m-t-20\">These activities work, and they are based in basic sciencethe fruits of decades of\n    research. Our research has been published in top-tier journals. Below are links to this research if you want to\n    learn more.</p>\n  <p class=\"research-para m-t-20\"> We receive requests from teachers across the country and the world about how to use\n    this research and couldnt keep up. So we designed this app to distill the research and the wisdom behind it into a\n    few easy steps.</p>\n</div>\n<div class=\"m-t-40 container-fluid m-p-0 text-center\">\n  <img class=\"m-t-40\" src=\"../../assets/images/architecture-building-campus-267853.png\" width=\"100%\" height=\"550px\">\n  <div class=\"container card-position m-b-40 cards-up\">\n    <div class=\"col-sm-4 flex-center\">\n      <div class=\"card-container\">\n        <div class=\"number\">1</div>\n        <div class=\"step\">STEP</div>\n        <img src=\"../../assets/images/002-calendar-1.svg\" class=\"m-t-20 m-b-20\" height=\"50px\" width=\"50px\">\n        <div class=\"card-text\">Record a date when you think students might feel stressed.</div>\n      </div>\n    </div>\n    <div class=\"col-sm-4 display-flex-row flex-center\">\n      <div class=\"card-container\">\n        <div class=\"number\">2</div>\n        <div class=\"step\">STEP</div>\n        <img src=\"../../assets/images/004-add.svg\" class=\"m-t-20 m-b-20\" height=\"60px\" width=\"48.89px\">\n        <div class=\"card-text\">Create a roster to identify which students you need to help.</div>\n      </div>\n    </div>\n    <div class=\"col-sm-4 flex-center\">\n      <div class=\"card-container\">\n        <div class=\"number\">3</div>\n        <div class=\"step\">STEP</div>\n        <img src=\"../../assets/images/001-email.svg\" class=\"m-t-20 m-b-20\" height=\"60px\" width=\"50px\">\n        <div class=\"card-text\">Sending writing activities that can help them.</div>\n      </div>\n    </div>\n  </div>\n  <button class=\"buttonStart\" (click)=\"navigateToPage('registration')\">GET STARTED</button>\n  <div class=\"m-t-20 m-b-40\">\n    <a class=\"learn-more\" (click)=\"navigateToPage('how-it-works')\">LEARN MORE</a>\n  </div>\n</div>\n<div class=\"container\">\n  <div class=\"heading m-t-20\">Research Papers</div>\n  <div style=\"margin-top:40px;\" *ngFor=\"let item of researchPapers\" class=\"row\">\n    <div class=\"col-sm-3\">\n      <img class=\"researchLogo\" [src]=\"item.img\" height=\"100px\" width=\"80%\">\n    </div>\n    <div class=\"col-sm-9\">\n      <a class=\"research-title\" target=\"_blank\" [href]=\"item.link\">{{item.title}}</a>\n      <p class=\"research-subtitle\">{{item.subtitle}}</p>\n      <p class=\"research-content\">{{item.content}}</p>\n    </div>\n  </div>\n</div>\n<app-footer></app-footer>\n");

